import React from 'react';

const skills = [
    "Java: Lenguaje que damos en mi formación académica donde lo usamos en NetBeans como también en Android Studio", 
    "React: Actualmente, estoy orientándome a esta tecnología para luego enfocarme en React Native", 
    "JavaScript: Esta, la utilizo junto a React por lo que también estoy en continuo aprendizaje con la misma, además utilizada para proyecto de bots", 
    "Python: Utilizada en términos básicos, más que nada en proyectos de la formación académica realizando páginas web usando Flask o Django ", 
    "SQL: Aprendida en la formación académica, aplicándola en MySQL y PostgreSQL", ];


export default () => {
 
  return(
    
    <div className="max-w-3xl mx-auto mt-6  pb-10 ">
        <header className="text-center text-gray-800">
          <h2 className="text-3xl font-medium py-2"> Tecnologias:</h2>
        </header>
        <div className=" mt-10 text-center ">
           
        <ul>
            {skills.map((skill, index) => <li className="text-justify text-lg text-gray-800 overflow-y-hidden p-4 text-lg font-light" key={index}>- {skill} </li> )}
        </ul>
       
        </div>
        
    </div>
  );
};